.hamGarlicInstructionTitle {
  color: #9b7ba8;
  font-size: 20px;
  margin-bottom: 5px;
}

div.hamGarlicLine {
  text-align: center;
  background-image: url(./images/line.png);
  width: 60%;
  height: 12px;
  background-repeat: no-repeat;
  background-size: contain;
}
div.hamGarlicSteps div.step1,
div.hamGarlicSteps div.step2,
div.hamGarlicSteps div.step3 {
  display: flex;
  justify-content: flex-start;
  color: #9b7ba8;
}

@media (min-width: 600px) {
  .hamGarlicInstructionTitle {
    font-size: 35px;
  }
  div.hamGarlicLine {
    height: 20px;
  }
}
