div.hamGarlicInformationsBackground {
  position: relative;
  padding: 10px 0;
  width: 100%;
  min-height: auto;
  background-position: inherit;
  background-size: auto;
  background-repeat: no-repeat;
  background-image: url(./images/skos3.png);
}

img.hamGarlicArrow {
  width: 3%;
  margin: 20px auto;
  display: block;
  transform: rotate(180deg);
  content: url("./images/arrow-mobile.png");
}

.hamGarlicNutritionalValues {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #fffad4;
  justify-content: space-around;
}

p.hamGarlicTitle {
  font-size: 20px;
  margin-bottom: 5px;
  color: #fffad4;
}

div.hamGarlicLineInformations {
  width: 180px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
  margin-bottom: 15px;
}
div.hamGarlicLineAlergens {
  width: 80px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
}

div.hamGarlicNutritionalValue {
  display: flex;
}

div.hamGarlicNutritionalValue p {
  flex-basis: 20%;
  margin: 0;
}

.hamGarlicAlergensDescription {
  color: #fffad4;
  display: none;
  transition: 0.3s;
}

@media (min-width: 768px) {
  div.hamGarlicLineInformations {
    width: 310px;
  }
  div.hamGarlicLineAlergens {
    width: 140px;
  }
  p.hamGarlicTitle {
    font-size: 35px;
  }
  div.hamGarlicNutritionalValue,
  .hamGarlicAlergensDescription {
    font-size: 18px;
  }
}

@media (min-width: 1024px) {
  img.hamGarlicArrow {
    margin: 20px auto;
    content: url("./images/arrow.png");
  }
  div.hamGarlicInformationsBackground {
    display: block;
    height: 0;
    background-image: none;
  }

  p.hamGarlicTitle {
    font-size: 23px;
    color: #9b7ba8;
    font-weight: 700;
  }
  div.hamGarlicLineInformations {
    width: 240px;
    height: 12px;
    background-image: url(./images/lineHamGarlic.png);
    background-size: contain;
    background-position: center;
  }

  div.hamGarlicLineAlergens {
    width: 120px;
    height: 11px;
    background-image: url(./images/lineHamGarlic.png);
    background-position: center;
  }
  .hamGarlicNutritionalValues {
    color: #9b7ba8;
    transition: 0.1s;
  }

  div.hamGarlicNutritionalValue,
  .hamGarlicAlergensDescription {
    font-size: 12px;
  }

  .hamGarlicAlergensDescription {
    color: #9b7ba8;
    display: block;
  }
}
