div.hamGarlicTopBackground {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 74vh;
  min-height: 360px;
  z-index: 3;
  background-image: url(../images/background-top-hamGarlic.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin-bottom: 120%;
}

div.hamGarlic {
  text-align: center;
  position: absolute;
  top: 8%;
  width: 100%;
}
div.hamGarlic img {
  width: 80%;
}

p.hamGarlicDescription {
  position: absolute;
  top: 100%;
  color: #9b7ba8;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  padding: 0px 40px 15px;
  margin-top: 50px;
}

.garlicPizzaAnimation {
  margin: 0 auto;
  background-position-y: 0%;
  background-size: 99.8%;
  background-repeat: no-repeat;
  margin-left: auto;
}

@media (min-width: 320px) {
  div.hamGarlicTopBackground {
    margin-bottom: 90%;
  }
}

@media (min-width: 360px) {
  p.hamGarlicDescription {
    padding: 15px 40px;
  }
}

@media (min-width: 500px) {
  p.hamGarlicDescription {
    font-size: 20px;
    margin-right: 5%;
    margin-left: 5%;
    margin-top: 80px;
  }
}
@media (min-width: 550px) {
  p.hamGarlicDescription {
    font-size: 26px;
  }
}

@media (min-width: 760px) {
  p.hamGarlicDescription {
    font-size: 30px;
  }
}

@media (min-width: 1024px) {
  .garlicPizzaAnimation {
    margin: 0;
    height: 100%;
    background-position-y: 0%;
    background-size: 100%;
    background-repeat: no-repeat;
    margin-left: auto;
  }

  div.hamGarlicTopBackground {
    width: 100%;
    height: 58vw;
    max-height: 1110px;
    background-image: url(../images/skos-desktop.png);
    background-repeat: no-repeat;
    background-size: contain;
    margin-bottom: 0;
  }

  div.hamGarlic {
    text-align: left;
    top: 110px;
    margin-left: 8%;
  }
  div.hamGarlic img {
    width: 30%;
  }
  p.hamGarlicDescription {
    color: #fffad4;
    top: 19%;
    left: 0;
    font-size: 16px;
    width: 38%;
  }

  div.ingredients_desktop img.desktop_hamGarlicIngredients {
    width: 23%;
    bottom: 15%;
    left: 12%;
  }
}
@media (min-width: 1200px) {
  p.hamGarlicDescription {
    font-size: 20px;
  }
}
@media (min-width: 1365px) {
  p.hamGarlicDescription {
    font-size: 22px;
  }
}
@media (min-width: 1439px) {
  p.hamGarlicDescription {
    font-size: 24px;
  }
}
@media (min-width: 1599px) {
  p.hamGarlicDescription {
    font-size: 25px;
  }
}
@media (min-width: 1920px) {
  p.hamGarlicDescription {
    font-size: 32px;
  }
}
